input {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 3px solid blue;
    border-radius: 5px;
    box-sizing: border-box;
}

#emailto:hover {
    text-decoration: underline;
    color: gray;
}

#message {
    width: 100%;
    height: 300px;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 3px solid blue;
    border-radius: 5px;
    box-sizing: border-box;
}

button {
    background-color: blue;
    color: white;
    border-radius: 5px;
}